:root {
  --base03: #002b36;
  --base02: #073642;
  --base01: #586e75;
  --base00: #657b83;
  --base0: #839496;
  --base1: #93a1a1;
  --base2: #eee8d5;
  --base3: #fdf6e3;
  --yellow: #b58900;
  --orange: #cb4b16;
  --red: #dc322f;
  --magenta: #d33682;
  --violet: #6c71c4;
  --blue: #268bd2;
  --cyan: #2aa198;
  --green: #859900;
}

body.dark {
  --b3: var(--base03);
  --b2: var(--base02);
  --b1: var(--base01);
  --b01: var(--base1);
  --b02: var(--base2);
  --b03: var(--base3);
}

body {
  --b3: var(--base3);
  --b2: var(--base2);
  --b1: var(--base1);
  --b01: var(--base01);
  --b02: var(--base02);
  --b03: var(--base03);

  background-color: var(--b3);
  font-family: monospace;
  font-size: large;
  color: var(--b01);
  text-align: center;
}

a:link {
  color: var(--blue);
}

a:visited {
  color: var(--blue);
}

#title {
  font-size: larger;
  background-color: var(--b2);
  display: inline-block;
  padding: 10px;
}

#navigation {
  padding: 10px;
}

#home-button {
  color: var(--red);
}

#teaching-button {
  color: var(--yellow);
}

#research-button {
  color: var(--green);
}

#cv-button {
  color: var(--blue);
}

#other-button {
  color: var(--violet);
}

.rectangle-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.rectangle {
  max-width: 600px;
  background-color: var(--b2);
  padding: 0 10px 10px 10px;
  margin: 10px;
}

h3 {
  font-weight: normal;
  text-decoration: underline;
}

ul {
  list-style-position: inside;
  text-align: left;
}

.ascii-art-container {
  display: flex;
  justify-content: center;
}

.ascii-art {
  text-align: left;
}